<template>
    <div class="main" id="main" @click="go">
        <Left></Left>
        <Mid></Mid>
        <Right></Right>
        
    </div>
</template>

<script>
    import Left from '../components/left'
    import Mid from '../components/mid'
    import Right from '../components/right'
    import {
        mapState, mapMutations
    } from 'vuex';
    export default {
        components: {
            Left,
            Mid,
            Right
        },
        computed: {
            ...mapState(['password', 'Loginstate'])
        },
        methods: {
            ...mapMutations(['loginStateDelete']),
            go() {
                console.log(123);
                if (!this.Loginstate) {
                    console.log(456);
                    this.$Message.warning('登陆状态过期，请重新登陆')
                    setTimeout(()=>{
                        this.$router.replace('/login')
                    },800)
                }
            },
            clearModule() {
                console.log("asdghjhgjkjkl");
                let Cookieparse = JSON.parse(document.cookie);
                Cookieparse.userpassword = "";
                this.loginStateDelete();
            },
            loginStateTest() {
                setInterval(() => {
                    console.log('5s');
                    let Cookieparse = JSON.parse(document.cookie);
                    if (new Date().getTime() - Cookieparse.logintime >= 60*60*1000) {
                        console.log("isTime");
                        this.clearModule();
                    }
                }, 10000);
            }
        },
        created() {
            this.loginStateTest()
        }
    }
</script>

