<template>
  <div id="app">
    <top></top>
    <router-view></router-view>
  </div>
</template>

<script>
  import top from "./components/top";
  import {
    mapState,
    mapMutations
  } from "vuex";
  export default {
    components: {
      top
    },
    methods: {
      ...mapMutations(["loginStateDelete", "loginStateActive"]),
      // 清理模块
      clearModule() {
        let Cookieparse = JSON.parse(document.cookie);
        Cookieparse.userpassword = "";
        document.cookie = JSON.stringify(Cookieparse)
        this.loginStateDelete();
        this.$router.replace("/");
      },
      init() {
        if (document.cookie.length > 0) {
          let Cookieparse = JSON.parse(document.cookie);
          if (new Date().getTime() - Cookieparse.logintime >= 60*60*1000) {
            this.clearModule();
          }
        } else {
          this.loginStateActive()
          this.$router.replace("/");
        }
      },
    },
    created() {
      this.init();
      // this.loginStateTest()
      // let Cookiecontent = document.cookie
      // let Cookieparse = JSON.parse(Cookiecontent);
      // let lastLogintime = Cookieparse.logintime
      // console.log(lastLogintime, Cookieparse);
      // this.delete()
    },
    computed: {
      ...mapState(["password", "Loginstate"])
    },
  };
</script>

