<template>
    <div class="chart_col chart_colleft">
        <div class="colleft_list" :class="cur==index?s:h" @mouseenter='mousein(index)' @mouseleave='mouseout(index)' v-for="(item,index) in historylist" :key="index">
            <div class="colleft_close" v-show="cur==index?t:f" @click="closelist(index)">X</div>
            <img :src="item.img" class="colleft_img">
            <p class="colleft_p">{{item.content}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                cur: 999,
                t: true,
                f: false,
                s: 'enter',
                h: 'leave',
                historylist: [{
                        img: '../assets/all.png',
                        content: 'asdasdasd'
                    },
                    {
                        img: '../assets/active.png',
                        content: '123123123'
                    },
                    {
                        img: '../assets/active.png',
                        content: '4564567788'
                    },
                    {
                        img: '../assets/active.png',
                        content: '99900000'
                    }
                ],
            }
        },
        methods: {
            closelist(x) {
                this.historylist.splice(x, 1)
            },
            mousein(x) {
                this.cur = x
            },
            mouseout(x) {
                this.cur = 999
            }
        }
    }
</script>

