<template>
    <div class=" chart_colmid">
        <div class="colmid_box">
            <div class="colmid_top">
                <div class="top_title">{{chartname}}</div>
                <div class="top_talk" ref="a">
                    <div :class="item.code==1?you:mine" v-for="(item,index) in talklist" :key="index" class="animated fadeIn">
                        <img :src="item.img" alt="" v-if="item.isimg" class="talklist_img" @dblclick="goBig(item.img)">
                        <p :class="item.code==1?yp:mp" v-else>{{item.p}}</p>
                        <img :class="item.code==1?yi:mi" :src="item.code==1?yimg:mimg">
                    </div>
                </div>
            </div>
            <div class="colmid_mid">
                <!-- <img src="../assets/pic.png" alt="" class="mid_imgbtn" @click="goUpfile"> -->
                <div class="mid_up">
                    <input @change="handleFileChange" type="file" ref="inputer" class="mid_imgbtn" accept="image/*">
                    <img src="../assets/pic.png" alt="" class="upimgbox">
                </div>
                <div class="mid_up">
                    <input @change="handleFileChange1" type="file" ref="inputer2" class="mid_filebtn">
                    <img src="../assets/file.png" alt="" class="upfilebox">
                </div>
            </div>
            <div class="colmid_bom">
                <textarea class="bom_textarea" v-model="text" @keyup.enter="send"></textarea>
                <button class="comit" @click="send">发送</button>
            </div>
            <div class="colmid_other">
                <div class="other_bigimg" v-if="isbigimg" id="bigimg">
                    <div class="bigimg_close" @click="closeBigimg">X</div>
                    <div class="bigimg_mark" id="bigimgmark"></div>
                    <img :src="bigimg" class="bigimg_img" id="bigimgImg">
                </div>
            </div>
        </div>
    </div>
</template>

<script scoped>
    import lrz from 'lrz'
    let Base64 = require('js-base64').Base64
    export default {
        data() {
            return {
                chartname: "分人",
                text: "",
                yp: "you_p",
                yi: "you_img",
                mp: "mine_p",
                mi: "mine_img",
                yimg: require("../assets/all.png"),
                mimg: require("../assets/active.png"),
                talklist: [],
                you: "talk_you",
                mine: "talk_mine",
                bigimg: require("../assets/active.png"),
                isbigimg: false
            };
        },
        methods: {
            handleFileChange1() {},
            handleFileChange() {
                let getfile = this.$refs.inputer.files[0];
                var reader = new FileReader();
                var img = new Image()
                console.log(reader);
                reader.onload = e => {
                    let imgFile = e.target.result;
                    console.log(imgFile);
                    lrz(getfile)
                        .then((res) => {
                            console.log(res.base64.length);
                            if (res.base64.length >= 2048 * 1024) {
                                alert('图片太大')
                                this.$message.warning('请输入正确的字符。')
                            } else {
                                if (imgFile.length != 0) {
                                    this.talklist.push({
                                        code: 0,
                                        isimg: true,
                                        img: res.base64
                                    })
                                }
                            }
                        })
                };
                reader.readAsDataURL(getfile);
            },
            send() {
                let textreg = /^[\u4E00-\u9FA5A-Za-z0-9=@%*！？，。、+“”（）]+$/
                if (textreg.test(this.text)) {
                    let sendText=Base64.encode(this.text)
                    // 输入的字符发送请求,发送成功在进行展示
                    let minetext = {
                        p: this.text,
                        code: 0
                    };
                    this.talklist.push(minetext);
                    this.keepBom();
                    this.text = "";
                } else {
                    this.$message.warning('请输入正确的字符')
                }
            },
            keepBom() {
                setTimeout(() => {
                    let b = this.$refs.a;
                    b.scrollTop = b.scrollHeight;
                }, 1);
            },
            goBig(x) {
                console.log(123);
                this.isbigimg = true;
                this.bigimg = x;
            },
            closeBigimg() {
                this.isbigimg = false;
            },
            clickBodyCloseImg() {
                let body = document.querySelector("body");
                body.addEventListener("click", e => {
                    if (e.target.id == "bigimgmark" || e.target.id == "bigimgImg") {
                        return;
                    } else {
                        this.isbigimg = false;
                    }
                });
            }
        },
        created() {
            this.clickBodyCloseImg();
        }
    };
</script>

