                if (this.username == '' || this.userpassword == '') {
                    this.$Message.warning('请输入用户名和密码');
                    return
                } else {
                    let test = /^[a-zA-Z0-9]{6,12}$/
                    if (test.test(this.username) && test.test(this.userpassword)) {
                        let url = 'https://api.shenjian.io/?appid=827aad4fcad024878809fbcb5d8e2c60'
                        let body = {
                            code: 300100,
                            date: 2012 - 12 - 19,
                            showContent: false
                        }
                        let a = login1(url, body).then((res) => {
                            
                            if (true) {
                                this.$Message.success('登陆成功');
                                setTimeout(() => {
                                    console.log(123);
                                    let Cookieparse = JSON.parse(document.cookie)
                                    console.log(456);
                                    Cookieparse.username = this.username
                                    Cookieparse.userpassword = this.userpassword
                                    Cookieparse.logintime = new Date().getTime()
                                    document.cookie = JSON.stringify(Cookieparse)
                                    this.loginStateActive();
                                    this.$router.replace('/main');
                                }, 800)
                            } else {
                                this.$Message.error('用户名和密码不正确');
                            }
                        })
                    } else {
                        if (!test.test(this.username) && !test.test(this.userpassword)) {
                            this.$Message.warning('用户名和密码请输入6到12位，字母数字，不能有符号');
                        } else {
                            if (!test.test(this.username)) {
                                this.$Message.warning('用户名请输入6到12位，字母数字，不能有符号');
                            }
                            if (!test.test(this.userpassword)) {
                                this.$Message.warning('用户名请输入6到12位，字母数字，不能有符号');
                            }
                        }
                    }
                }
            },
            // 默认检测
            init() {
                if (document.cookie.length == 0) {
                    document.cookie = '{"username":"","userpassword":"","logintime":""}'
                    console.log(document.cookie);
                    console.log('什么也没有');
                } else {
                    let Cookieparse = JSON.parse(document.cookie);
                    this.username = Cookieparse.username
                    this.userpassword = Cookieparse.userpassword
                }
            }
        },
        created() {
            this.init()
        },
        computed: {
            ...mapState(['Loginurl'])
        }
    }
</script>

