import Vue from 'vue'
import Router from 'vue-router'
import Main from './views/main.vue'
import Login from './views/login.vue'


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/main',
      name: 'main',
      component: Main
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },

    {
      path: '/',
      redirect: 'login'
    },
  ]
})